import { makeAutoObservable, configure } from 'mobx';
import isMobile from 'ismobilejs';
import intl from 'react-intl-universal';
import Config from '../config';

configure({
  enforceActions: 'never'
});

class Store {
  isPhone = false;
  urlHash = '';

  lang = '';
  supportedLocales = ['en-US', 'zh-HK'];

  constructor() {
    makeAutoObservable(this);

    this.reloadInfo();
    this.loadLocales();
  }

  reloadInfo = async ({ once = false } = {}) => {
    this.isPhone = isMobile(window.navigator).phone || window.innerWidth <= Config.RESPONSIVE_MOBILE;

    if (once) return;
    setTimeout(this.reloadInfo, 6000);
  };

  setUrlHash = hash => {
    this.urlHash = hash;
  };

  loadLocales = () => {
    let currentLocale = intl.determineLocale({
      localStorageLocaleKey: 'lang'
    });

    this.setLanguage(currentLocale);
  };

  setLanguage = lang => {
    if (!this.supportedLocales.includes(lang)) {
      lang = 'en-US';
    }

    this.lang = lang;
    window.localStorage.setItem('lang', lang);

    const locales = {
      'en-US': require('../locales/en-US.js').default,
      'zh-HK': require('../locales/zh-HK.js').default
    };

    intl.init({
      currentLocale: lang,
      locales
    });
  };
}

const store = new Store();
export default store;
