import React, { useState, useRef, useEffect } from 'react';
import { Observer, useLocalObservable } from 'mobx-react';
import intl from 'react-intl-universal';
import { Select, ConfigProvider } from 'antd';
import {
  HeaderAppBtnStyled,
  HeaderAppStyled,
  HeaderLangStyled,
  HeaderMainStyled,
  HeaderMenusStyled,
  HeaderMenuStyled,
  HeaderStyled,
  HEADER_HEIGHT_AMOUNT
} from './styled';
import { Layout } from '../common';
import Stores from '../../stores';
import Config from '../../config';

import LogoMinImg from '../../images/logoMin.svg';
import LogoImg from '../../images/logoText.svg';

function App() {
  const localStore = useLocalObservable(() => Stores);
  const [headerBg, setHeaderBg] = useState(false);
  const curRef = useRef(null);
  curRef.current = () => {
    setHeaderBg(false);
  };

  const scroll = id => {
    const element = document.getElementById(id);
    if (element) {
      element.scrollIntoView({
        behavior: 'smooth',
        inline: 'nearest'
      });
    } else {
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      });
    }
  };

  useEffect(() => {
    window.onscroll = () => {
      const scrollTop = Math.max(document.body.scrollTop, document.documentElement.scrollTop);
      if (scrollTop > HEADER_HEIGHT_AMOUNT / 2) {
        setHeaderBg(true);
      } else {
        curRef.current();
      }
    };
  }, []);

  return (
    <Observer>
      {() => (
        <HeaderMainStyled bg={headerBg}>
          <Layout>
            <HeaderStyled>
              <HeaderMenuStyled logo onClick={() => scroll('hero')}>
                <img src={localStore.isPhone ? LogoMinImg : LogoImg} alt="" />
              </HeaderMenuStyled>
              {localStore.isPhone ? (
                <></>
              ) : (
                <HeaderMenusStyled>
                  <HeaderMenuStyled onClick={() => scroll('hero')}>{intl.get('header.home')}</HeaderMenuStyled>
                  <HeaderMenuStyled onClick={() => scroll('buidl')}>{intl.get('header.buidl')}</HeaderMenuStyled>
                  <HeaderMenuStyled onClick={() => scroll('hodl')}>{intl.get('header.hodl')}</HeaderMenuStyled>
                  <HeaderMenuStyled onClick={() => scroll('team')}>{intl.get('header.team')}</HeaderMenuStyled>
                </HeaderMenusStyled>
              )}
              <HeaderAppStyled>
                <HeaderAppBtnStyled href={Config.launchApp} target={intl.get('header.app')}>
                  {intl.get('header.app')}
                </HeaderAppBtnStyled>

                <HeaderLangStyled id="header-lang">
                  <ConfigProvider
                    theme={{
                      token: {
                        colorPrimary: '#9b9b9b'
                      }
                    }}
                  >
                    <Select
                      getPopupContainer={() => document.getElementById('header-lang')}
                      defaultValue={localStore.lang}
                      onChange={_ => {
                        localStore.setLanguage(_);
                        window.location.reload();
                      }}
                      options={localStore.supportedLocales.map(v => {
                        return { value: v, label: intl.get(v), title: '' };
                      })}
                    />
                  </ConfigProvider>
                </HeaderLangStyled>
              </HeaderAppStyled>
            </HeaderStyled>
          </Layout>
        </HeaderMainStyled>
      )}
    </Observer>
  );
}

export default App;
